/* font */
@font-face {
    font-family: 'kdg_Medium';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts-20-12@1.0/kdg_Medium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}

/*
reset css start */

/* reset.css */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
    font-family: 'Pretendard-Regular';
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

/* reset css end */


/* css [[main]] */
.mainBox{
    background-color: antiquewhite;
    color: rgb(51, 49, 63);
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    overflow: hidden;
}
.mainBox .mainText{
    font-family: 'kdg_Medium';
    font-size: 40px;
    margin-bottom: 40px;
}
.mainBox .subText{
    margin-bottom: 30px;
    line-height: 25px;
    font-size: 18px;
}
.mainBox .btnBox{
    display: flex;
    width: 100vw;
    gap: 20px;
    margin-bottom: 20px;
    justify-content: center;
    align-items: center;
}
.mainBox .btn{
    background-color: rgb(51, 49, 63);
    color:  antiquewhite;
    padding: 10px 45px;
    font-size: 14px;
    cursor: pointer;
    transition: all .2s ease;
}
.mainBox .btn:hover{
    color: rgb(51, 49, 63);
    background-color:  antiquewhite;
}
.mainBox .miniText{
    font-size: 14px;
}

/* 결과창 css */
.mainBox .resultWrap{
    display: flex;
    width: 100vw;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.mainBox .resultCard{
    width: 300px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}
.mainBox .humanImg{
    width: 100%;
    height: 435px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
}
.mainBox .humanImg img{
    /* width: 100%; */
    height: 100%;
}
.mainBox .resultWrap .resultCard .jinName{
    color:   antiquewhite;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.mainBox .resultWrap .resultCard1 .jinName{
    background-color: rgb(40, 40, 168);
}
.mainBox .resultWrap .resultCard2 .jinName{
    background-color: rgb(42, 110, 39);
}
.mainBox .resultWrap .resultCard3 .jinName{
    background-color: rgb(177, 54, 32);
}
.mainBox .resultWrap .resultCard4 .jinName{
    background-color: rgb(43, 36, 41);
}
.mainBox .humanName{
    background-color: rgb(51, 49, 63);
    color: antiquewhite;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
}
.mainBox .numText{
    font-size: 14px;
}
.mainBox .textWrapper{
    margin-bottom: 20px;
}

@media (max-width: 767px){
    .mainBox{
        /* overflow-y:initial; */
        height: auto;
        overflow-x: hidden;
        overflow-y: initial;
        padding: 50px 0px;
    }
    .mainBox.mainTextBox{
        height: 100vh;
        padding: 0px 0px;
    }
    .mainBox .mainText{
        font-size: 30px;
    }
    .mainBox .subText{
        font-size: 16px;
        padding: 0px 10%;
    }
	.mainBox .btnBox{
        flex-direction: column;
    }
    .mainBox .btnBox .btn{
        width: 90%;
    }
    .mainBox .resultWrap{
       flex-wrap: wrap;
       padding: 0;

    }
    .mainBox .resultCard{
        width: 45%;
    }
    .mainBox .humanImg{
        /* height: 250px; */
        height: 30vh;
        min-height: 250px;
    }
    .mainBox .humanImg img{
        width: 100%;
        height: auto;
    }
    .mainBox .btn:hover{
        color: antiquewhite ;
        background-color:  rgb(51, 49, 63);
    }
}
